<template>
  <div class="gain-content">
    <div class="header-content">
      <pageTitle :title="pageName"></pageTitle>
          </div>
    <div class="main-content">
      <div class="block-content-table">
        <min-title nameVal="毛利概览-直营" :show="'3'" :type="'gain-zy-mlglfx'" @tabsType="tabsType"></min-title>
        <table-list :tableData="mlglTableData" :type="'gain-mlgl'"></table-list>
      </div>
      <div class="block-content-table">
        <min-title
          :nameVal="mlfxTitle"
          :show="'1'"
          :type="'gain-zy-mlfx'"
          :tabsData="blockSwitchList"
          @tabsType="tabsType"
        ></min-title>
        <table-list
          :tableData="mlfxTableData"
          :type="'gain-mlfx'"
          @changeTableOrder="changeTableOrder"
          :needClick="true"
          @changeTableRow="changeTableRow"
        ></table-list>
      </div>
      <div class="block-content-chart">
        <min-title
          :nameVal="qsfxTitle"
          :show="'3'"
        ></min-title>
        <div class="block-chart">
          <BarLineChart
            :chart-data="qsfxLineData"
            :option="qsfxLineOption(qsfxLineData)"
          ></BarLineChart>
        </div>
      </div>
      <div class="block-content-chart">
        <min-title
          :nameVal="ystbfxTitle"
          :tabsData="blockSwitchList"
          :show="'1'"
          :type="'gain-zy-ystbfx'"
          @tabsType="tabsType"
        ></min-title>
        <div class="block-chart">
          <BarLineChart
            :chart-data="ystbfxLineData"
            :option="ystbfxBarLineOption(ystbfxLineData)"
          ></BarLineChart>
        </div>
      </div>
      <div class="block-content-table">
        <min-title
          :nameVal="pmfxTitle"
          :tabsData="blockSwitchList"
          :type="'gain-zy-pmfx'"
          @tabsType="tabsType"
          :show="'1'"
        ></min-title>
        <type-list
          :tabsData="pmfxTypeList"
          @activeTypeName=changePmfxType
        ></type-list>
        <table-list
          :tableData="pmfxTableData"
          :type="'gain-pmfx'"
          @changeTableOrder="changeTableOrder"
        ></table-list>
      </div>
    </div>
  </div>
</template>

<script>
import pageTitle from '../../components/common/pageTitle'
import MinTitle from '../../components/common/minTitle'
import TableList from '../../components/common/tableList'
import TypeList from '../../components/common/typeList'
import BarLineChart from '../../components/echarts/BarLineChart'
import _ from 'lodash'
import { px2px } from '../../assets/js/lib/common/flexible' // 在echarts的option配置中，用到的字体大小，柱图宽度等数字时，要用此方法包起来
import {initNum, compareSort, formatNumber, switchPercent} from '../../assets/js/core/weapon' // 引入排序方法
import apiService from '../../assets/js/api'
export default {
  name: '',
  data () {
    return {
      pageName: '扣前毛利(万元)', // 页面的名字
      mlfxTitle: '毛利分析-子公司',
      qsfxTitle: '趋势分析',
      ystbfxTitle: '因素同比分析',
      pmfxTitle: '排名分析',
      mlfxZgsTableDataInit: [],
      qsfxBarLineDataInit: [],
      ystbfxBarLineDataInit: [],
      pmfxTableDataInit: [],
      pmfxIndex: 0,
      pmfxTypeIndex: 0,
      mlfxZgsIndex: 0,
      ystbfxIndex: 0,
      pmfxTypeList: [{name: '店铺', value: '店铺'}, {name: '大类', value: '大类'}],
      pmfxTableData: {
        tableHeader: [
          {name: '店铺', prop: 'dp', needOrder: false, order: '0', isPresent: false, needArrow: false},
          {name: '收入同比', prop: 'srtb', needOrder: true, order: '2', isPresent: true, needArrow: true, newLine: true},
          {name: '成本同比', prop: 'cbtb', needOrder: true, order: '0', isPresent: true, needArrow: true, newLine: true},
          {name: '毛利率', prop: 'mll', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
          {name: '同期', prop: 'tq', needOrder: true, order: '0', isPresent: true, needArrow: true},
          {name: '差异', prop: 'cy', needOrder: true, order: '0', isPresent: true, needArrow: true},
          {name: '占比', prop: 'zb', needOrder: true, order: '0', isPresent: true, needArrow: false}
        ],
        tableContent: []
      },
      blockSwitchList: [
        { name: '月', value: 'M' },
        { name: '季', value: 'J' },
        { name: '年累', value: 'NL' }
      ], // 每个小块的时间切换按钮
      ystbfxLineData: [],
      qsfxLineData: [],
      mlglTableData: {
        tableHeader: [
          {
            name: '时间',
            prop: 'sj',
            needOrder: false,
            order: '0',
            isPresent: false,
            needArrow: false
          },
          {
            name: '收入',
            prop: 'sr',
            needOrder: false,
            order: '1',
            isPresent: false,
            needArrow: false
          },
          {
            name: '毛利',
            prop: 'ml',
            needOrder: false,
            order: '0',
            isPresent: false,
            needArrow: false
          },
          {
            name: '毛利率',
            prop: 'mll',
            needOrder: false,
            order: '0',
            isPresent: true,
            needArrow: false
          },
          {
            name: '差异',
            prop: 'cy',
            needOrder: false,
            order: '0',
            isPresent: true,
            needArrow: true
          }
        ],
        tableContent: []
      },
      mlfxTableData: {
        tableHeader: [
          {
            name: '子公司',
            prop: 'zgs',
            needOrder: false,
            order: '0',
            isPresent: false,
            needArrow: false
          },
          {
            name: '收入',
            prop: 'sr',
            needOrder: true,
            order: '2',
            isPresent: false,
            needArrow: false
          },
          {
            name: '毛利',
            prop: 'ml',
            needOrder: true,
            order: '0',
            isPresent: false,
            needArrow: false
          },
          {
            name: '毛利率',
            prop: 'mll',
            needOrder: true,
            order: '0',
            isPresent: true,
            needArrow: false,
            newLine: false
          },
          {
            name: '同期',
            prop: 'tq',
            needOrder: true,
            order: '0',
            isPresent: true,
            needArrow: true
          },
          {
            name: '差异',
            prop: 'cy',
            needOrder: true,
            order: '0',
            isPresent: true,
            needArrow: true
          },
          {
            name: '占比',
            prop: 'zb',
            needOrder: true,
            order: '0',
            isPresent: true,
            needArrow: false
          }
        ],
        tableContent: []
      }
    }
  },
  computed: {
    dateStr () {
      return this.$store.state.dateStr
    },
    dateStrM () {
      return this.$store.getters.dateStrM
    },
    dateStrJ () {
      return this.$store.getters.dateStrJ
    }
  },
  components: {
    pageTitle,
    MinTitle,
    TableList,
    BarLineChart,
    TypeList
  },
  /**
  watch: {
    dateStr: {
      handler () {
        this.getAllData()
      }
    }
  }, **/
  created () {
    this.$store.commit('updateHeaderTitle', '扣前毛利')
    this.$store.commit('updateShowIcon', true)
    if (this.dateStr !== '') {
      this.getAllData()
    }
    this.$store.commit('updateDateStr', this.$store.state.dateStrInit)
    this.$store.dispatch('updateUserOption', {USER_ACCOUNT: window.userAccount, EXAMINATION_SUBJECT: '直营', LEVEL: '总监', URL: '毛利'})
  },
  mounted () {
    this.$eventBus.$on('userSelected', this.getAllData)
  },
  beforeDestroy () {
    this.$eventBus.$off('userSelected', this.getAllData)
  },
  methods: {
    tabsType ({ activeValue, name, type, index }) {
      if (type === 'gain-zy-mlfx') {
        this.mlfxZgsIndex = index
        this.dealMlfxZgsTableData()
      }
      if (type === 'gain-zy-ystbfx') {
        this.ystbfxIndex = index
        this.dealYstbfxBarLineData()
      }
      if (type === 'gain-zy-pmfx') {
        this.pmfxIndex = index
        this.dealPmfxTableData()
      }
    },
    changePmfxType ({index, activeTypeName}) {
      this.pmfxTypeIndex = index
      this.dealPmfxTableData()
    },
    ystbfxBarLineOption (dataArr) {
      const labelList = []
      const valueList = []
      for (let item of this.ystbfxLineData) {
        labelList.push(item.label)
        valueList.push(item.value)
      }
      const option = {
        legend: {
          show: true,
          textStyle: {
            fontFamily: 'PingFangSC-Regular',
            color: '#71768C',
            fontSize: px2px(24)
          }
        },
        xAxis: [
          {
            type: 'category',
            name: '',
            interval: 0,
            nameLocation: 'start',
            nameTextStyle: {
              color: '#71768C',
              fontSize: px2px(24),
              padding: [0, 0, px2px(-30), px2px(-80)]
            },
            data: [],
            axisPointer: {
              type: 'shadow'
            },
            axisLabel: {
              show: true,
              interval: 0,
              textStyle: {
                color: '#71768C'
              },
              fontSize: px2px(24)
            },
            axisLine: {
              show: true,
              lineStyle: {
                color: '#98FB98'
              }
            },
            axisTick: {
              show: false
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '',
            nameTextStyle: {
              color: '#71768C',
              fontSize: px2px(24)
            },
            position: 'right',
            axisLabel: {
              show: false,
              fontSize: px2px(24),
              textStyle: {
                color: '#71768C'
              },
              formatter: '{value}%'
            },
            axisLine: {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            splitLine: {
              show: false,
              lineStyle: {
                color: '#7d838b'
              }
            },
            axisTick: {
              show: false
            }
          }
        ],
        series: [
          {
            type: 'bar',
            name: '收入同比',
            smooth: true,
            barWidth: px2px(80),
            symbol: 'none',
            yAxisIndex: 0,
            label: {
              show: true,
              position: 'top',
              fontFamily: 'PingFangSC-Regular',
              color: '#71768C',
              fontSize: px2px(24),
              formatter: function (param) {
                // console.info(initNum(param.value) === 0)
                if (initNum(param.value) === 0) {
                  return ''
                } else {
                  return param.value + '%'
                }
              }
            },
            barGap: '-100%',
            itemStyle: {
              color: '#FFAB00'
            },
            data: []
          },
          {
            type: 'bar',
            name: '成本同比',
            smooth: true,
            label: {
              show: true,
              position: 'top',
              fontFamily: 'PingFangSC-Regular',
              color: '#71768C',
              fontSize: px2px(24),
              formatter: function (param) {
                if (initNum(param.value) === 0) {
                  return ''
                } else {
                  return param.value + '%'
                }
              }
            },
            barWidth: px2px(80),
            symbol: 'none',
            yAxisIndex: 0,
            itemStyle: {
              color: '#0000FF'
            },
            data: []
          },
          {
            type: 'bar',
            name: '毛利率差异',
            smooth: true,
            label: {
              show: true,
              position: 'top',
              fontFamily: 'PingFangSC-Regular',
              color: '#71768C',
              fontSize: px2px(24),
              formatter: function (param) {
                if (initNum(param.value) === 0) {
                  return ''
                } else {
                  return param.value + '%'
                }
              }
            },
            barWidth: px2px(80),
            symbol: 'none',
            yAxisIndex: 0,
            itemStyle: {
              color: '#87CEFA'
            },
            data: []
          }
        ]
      }

      option.xAxis[0].data = labelList
      option.series[0].data[0] = {value: valueList[0], itemStyle: {barBorderRadius: parseFloat(valueList[0]) > 0 ? [px2px(40), px2px(40), 0, 0] : [0, 0, px2px(40), px2px(40)]}}
      option.series[0].data[1] = {value: valueList[1], itemStyle: {barBorderRadius: parseFloat(valueList[1]) > 0 ? [px2px(40), px2px(40), 0, 0] : [0, 0, px2px(40), px2px(40)], color: '#0000FF'}}
      option.series[0].data[2] = {value: valueList[2], itemStyle: {barBorderRadius: parseFloat(valueList[2]) > 0 ? [px2px(40), px2px(40), 0, 0] : [0, 0, px2px(40), px2px(40)], color: '#87CEFA'}}
      return option
    },
    qsfxLineOption (dataArr) {
      const option = {
        legend: {
          show: true,
          textStyle: {
            fontFamily: 'PingFangSC-Regular',
            color: '#71768C',
            fontSize: px2px(24)
          }
        },
        xAxis: [
          {
            type: 'category',
            name: '',
            interval: 0,
            nameLocation: 'start',
            nameTextStyle: {
              color: '#71768C',
              fontSize: px2px(24),
              padding: [0, 0, px2px(-30), px2px(-80)]
            },
            data: [],
            axisPointer: {
              type: 'shadow'
            },
            axisLabel: {
              show: true,
              interval: 0,
              textStyle: {
                color: '#71768C'
              },
              fontSize: px2px(24)
            },
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '',
            nameTextStyle: {
              color: '#71768C',
              fontSize: px2px(24)
            },
            axisLabel: {
              show: true,
              fontSize: px2px(24),
              textStyle: {
                color: '#71768C'
              },
              formatter: '{value}%'
            },
            axisLine: {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            splitLine: {
              show: false,
              lineStyle: {
                color: '#7d838b'
              }
            },
            axisTick: {
              show: false
            }
          },
          {
            type: 'value',
            name: '',
            nameTextStyle: {
              color: '#71768C',
              fontSize: px2px(24)
            },
            show: true,
            axisLabel: {
              show: true,
              fontSize: px2px(24),
              textStyle: {
                color: '#71768C'
              },
              formatter: '{value}%'
            },
            axisLine: {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            splitLine: {
              show: false,
              lineStyle: {
                color: '#213A6B'
              }
            },
            axisTick: {
              show: false
            }
          }
        ],
        series: [
          {
            type: 'bar',
            name: '毛利率差异',
            barWidth: px2px(20),
            itemStyle: {
              color: '#34C9F8 '
            },
            data: []
          },
          {
            type: 'line',
            name: '毛利率',
            symbol: 'none',
            smooth: true,
            yAxisIndex: 1,
            itemStyle: {
              color: '#0000FF'
            },
            lineStyle: {
              color: '#0000FF',
              type: 'solid',
              width: px2px(4)
            },
            data: []
          },
          {
            type: 'line',
            name: '同期毛利率',
            yAxisIndex: 1,
            smooth: true,
            symbol: 'none',
            itemStyle: {
              color: '#FFD700'
            },
            lineStyle: {
              color: '#FFD700',
              type: 'solid',
              width: px2px(4)
            },
            data: []
          }
        ]
      }
      const labelList = []
      const mllcyList = []
      const mllList = []
      const tqmllList = []
      for (let item of this.qsfxLineData) {
        labelList.push(item.label)
        mllcyList.push(item.mllcy)
        mllList.push(item.mll)
        tqmllList.push(item.tqmll)
      }
      option.xAxis[0].data = labelList
      for (let i = 0; i < mllcyList.length; i++) {
        option.series[0].data[i] = {value: mllcyList[i], itemStyle: {color: parseFloat(mllcyList[i]) > 0 ? '#34C9F8' : '#FF0000'}}
      }
      option.series[1].data = mllList
      option.series[2].data = tqmllList
      return option
    },
    changeTableRow ({row, name, type}) {
      // 当点击了表格的某一行，进行联动操作
      // this.mlfxTitle = '毛利分析-' + name
      this.qsfxTitle = '趋势分析-' + name
      this.getQsfxBarLineData(name)
      this.ystbfxTitle = '因素同比分析-' + name
      this.getYstbfxBarLineData(name)
      this.pmfxTitle = '排名分析-' + name
      // 要触发下面排名分析的查询
      this.getPmfxTableData(name)
    },
    changeTableOrder ({ header, type }) {
      // debugger
      let tempTable = { tableHeader: [], tableContent: [] }
      if (type === 'gain-mlfx') {
        // 这个来分辨哪个表格需要排序
        tempTable = _.cloneDeep(this.mlfxTableData)
      }
      if (type === 'gain-pmfx') {
        tempTable = _.cloneDeep(this.pmfxTableData)
      }
      const headerList = []
      let tableContent = []
      for (let item of tempTable.tableHeader) {
        if (header.order === '0') {
          // 说明想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '1') {
          // 说明之前是升序，想要降序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '2'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '2') {
          // 说明之前是降序，想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        }
        headerList.push(item)
      }
      if (header.order === '0') {
        // 说明想要升序
        tableContent = tempTable.tableContent.sort(
          compareSort(header.prop, true)
        )
      } else if (header.order === '1') {
        // 说明之前是升序，想要降序
        tableContent = tempTable.tableContent.sort(
          compareSort(header.prop, false)
        )
      } else if (header.order === '2') {
        // 说明之前是降序，想要升序
        tableContent = tempTable.tableContent.sort(
          compareSort(header.prop, true)
        )
      }
      if (type === 'gain-mlfx') {
        // 这个来分辨哪个表格需要排序
        this.mlfxTableData.tableHeader = headerList
        this.mlfxTableData.tableContent = tableContent
      }
      if (type === 'gain-pmfx') {
        this.pmfxTableData.tableHeader = headerList
        this.pmfxTableData.tableContent = tableContent
      }
    },
    // 下面是查询数据的方法
    getAllData () {
      this.getMlglTableData()
      this.getMlfxZgsTableData()
    },
    // 毛利概览的数据
    async getMlglTableData () {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.gain.getMlglTableData, {dateStrM: this.dateStrM})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        const list = []
        for (let item of result.res) {
          const obj = {}
          obj.sj = item.period_wid
          obj.sr = formatNumber(initNum(item.income_amt).toFixed(0), 0)
          obj.ml = formatNumber(initNum(item.margin_amt).toFixed(0), 0)
          obj.mll = (initNum(item.margin_rate) * 100).toFixed(1)
          obj.cy = (initNum(item.margin_yoy) * 100).toFixed(1)
          list.push(obj)
        }
        this.mlglTableData.tableContent = list
        // this.ystbfxLineDataInit = result
        // 要根据选择的月，季，年累，年，进行数据的整理
        // this.dealystbfxLineData()
      }
    },
    async getMlfxZgsTableData () {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.gain.getMlfxZgsTableData, {dateStrM: this.dateStrM})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.mlfxZgsTableDataInit = result
        this.dealMlfxZgsTableData()
      }
    },
    dealMlfxZgsTableData () {
      const list = []
      let dataList = []
      if (this.mlfxZgsIndex === 0) {
        dataList = this.mlfxZgsTableDataInit.resM || []
      } else if (this.mlfxZgsIndex === 1) {
        dataList = this.mlfxZgsTableDataInit.resJ || []
      } else if (this.mlfxZgsIndex === 2) {
        dataList = this.mlfxZgsTableDataInit.resNl || []
      }
      for (let item of dataList) {
        const obj = {}
        obj.zgs = item.distribution_name
        obj.sr = formatNumber(initNum(item.income_amt).toFixed(0), 0)
        obj.ml = formatNumber(initNum(item.margin_amt).toFixed(0), 0)
        obj.mll = switchPercent(item.margin_rate) // (initNum(item.margin_rate) * 100).toFixed(0)
        obj.tq = switchPercent(item.margin_yoy) // (initNum(item.margin_yoy) * 100).toFixed(0)
        obj.cy = switchPercent(item.deviation) // (initNum(item.deviation) * 100).toFixed(0)
        obj.zb = switchPercent(item.rotio_rate) // (initNum(item.rotio_rate) * 100).toFixed(0)
        list.push(obj)
      }
      this.mlfxTableData = {
        // 这里是为了重置排序，以第一个排序列开始排序
        tableHeader: [
          {
            name: '子公司',
            prop: 'zgs',
            needOrder: false,
            order: '0',
            isPresent: false,
            needArrow: false
          },
          {
            name: '收入',
            prop: 'sr',
            needOrder: true,
            order: '2',
            isPresent: false,
            needArrow: false
          },
          {
            name: '毛利',
            prop: 'ml',
            needOrder: true,
            order: '0',
            isPresent: false,
            needArrow: false
          },
          {
            name: '毛利率',
            prop: 'mll',
            needOrder: true,
            order: '0',
            isPresent: true,
            needArrow: false,
            newLine: false
          },
          {
            name: '同期',
            prop: 'tq',
            needOrder: true,
            order: '0',
            isPresent: true,
            needArrow: true
          },
          {
            name: '差异',
            prop: 'cy',
            needOrder: true,
            order: '0',
            isPresent: true,
            needArrow: true
          },
          {
            name: '占比',
            prop: 'zb',
            needOrder: true,
            order: '0',
            isPresent: true,
            needArrow: false
          }
        ],
        tableContent: list.sort(compareSort('sr', false))
      }
      if (this.mlfxTableData.tableContent.length > 0) {
        this.qsfxTitle = '趋势分析-' + this.mlfxTableData.tableContent[0].zgs
        this.ystbfxTitle = '因素同比分析-' + this.mlfxTableData.tableContent[0].zgs
        this.pmfxTitle = '排名分析-' + this.mlfxTableData.tableContent[0].zgs
        this.$eventBus.$emit('resetSelectedRow', {type: 'gain-mlfx', name: this.mlfxTableData.tableContent[0].zgs})
        this.getQsfxBarLineData(this.mlfxTableData.tableContent[0].zgs)
        this.getYstbfxBarLineData(this.mlfxTableData.tableContent[0].zgs)
        this.getPmfxTableData(this.mlfxTableData.tableContent[0].zgs)
      } else {
        this.qsfxTitle = '趋势分析'
        this.ystbfxTitle = '因素同比分析'
        this.pmfxTitle = '排名分析'
        this.qsfxLineData = []
        this.ystbfxLineData = []
        this.pmfxTableData.tableContent = []
      }
    },
    async getQsfxBarLineData (companyName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.gain.getQsfxZgsBarLineData, {subName: companyName, dateStrM: this.dateStrM})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.qsfxBarLineDataInit = result.res
        this.dealQsfxBarLineData()
        // console.info(result.res)
      }
    },
    dealQsfxBarLineData () {
      const list = []
      let dataList = []
      dataList = this.qsfxBarLineDataInit || []
      // console.info(dataList)
      for (let item of dataList) {
        const obj = {}
        obj.label = item.period_wid
        obj.mll = switchPercent(item.margin_rate) // (initNum(item.margin_rate) * 100).toFixed(0)
        obj.tqmll = switchPercent(item.margin_yoy) // (initNum(item.margin_yoy) * 100).toFixed(0)
        obj.mllcy = switchPercent(item.deviation) // (initNum(item.deviation) * 100).toFixed(0)
        // obj.name = name
        list.push(obj)
      }
      this.qsfxLineData = list
    },
    async getYstbfxBarLineData (companyName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.gain.getYstbfxBarLineData, {subName: companyName, dateStrM: this.dateStrM})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        // console.info(result)
        this.ystbfxBarLineDataInit = result
        this.dealYstbfxBarLineData()
      }
    },
    dealYstbfxBarLineData () {
      const list = []
      let dataList = []
      if (this.ystbfxIndex === 0) {
        dataList = this.ystbfxBarLineDataInit.resM || []
      } else if (this.ystbfxIndex === 1) {
        dataList = this.ystbfxBarLineDataInit.resJ || []
      } else if (this.ystbfxIndex === 2) {
        dataList = this.ystbfxBarLineDataInit.resNl || []
      }
      if (dataList.length > 0) {
        const item = dataList[0]
        const obj1 = {}
        const obj2 = {}
        const obj3 = {}
        obj1.label = '收入'
        obj1.value = (initNum(item.income_yoy) * 100).toFixed(1)
        obj2.label = '成本'
        obj2.value = (initNum(item.cost_yoy) * 100).toFixed(1)
        obj3.label = '毛利率差异'
        obj3.value = (initNum(item.deviation) * 100).toFixed(1)
        list[0] = obj1
        list[1] = obj2
        list[2] = obj3
        this.ystbfxLineData = list
      } else {
        this.ystbfxLineData = []
      }
    },
    async getPmfxTableData (companyName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.gain.getPmfxTableData, {subName: companyName, dateStrM: this.dateStrM})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.pmfxTableDataInit = result
        this.dealPmfxTableData()
      }
    },
    dealPmfxTableData () {
      const list = []
      let dataList = []
      if (this.pmfxTypeIndex === 0) {
        if (this.pmfxIndex === 0) {
          dataList = this.pmfxTableDataInit.resshopM || []
        } else if (this.pmfxIndex === 1) {
          dataList = this.pmfxTableDataInit.resshopJ || []
        } else if (this.pmfxIndex === 2) {
          dataList = this.pmfxTableDataInit.resshopNl || []
        }
      } else if (this.pmfxTypeIndex === 1) {
        if (this.pmfxIndex === 0) {
          dataList = this.pmfxTableDataInit.resstM || []
        } else if (this.pmfxIndex === 1) {
          dataList = this.pmfxTableDataInit.resstJ || []
        } else if (this.pmfxIndex === 2) {
          dataList = this.pmfxTableDataInit.resstNl || []
        }
      }
      for (let item of dataList) {
        const obj = {}
        obj.dp = item.type_name
        obj.srtb = (initNum(item.income_yoy) * 100).toFixed(1)
        obj.cbtb = (initNum(item.cost_yoy) * 100).toFixed(1)
        obj.mll = (initNum(item.margin_rate) * 100).toFixed(1)
        obj.tq = (initNum(item.margin_yoy) * 100).toFixed(1)
        obj.cy = (initNum(item.deviation) * 100).toFixed(1)
        obj.zb = (initNum(item.rotio_rate) * 100).toFixed(1)
        list.push(obj)
      }
      this.pmfxTableData = {
        // 这里是为了重置排序，以第一个排序列开始排序
        tableHeader: [
          {name: this.pmfxTypeIndex === 0 ? '店铺' : '大类', prop: 'dp', needOrder: false, order: '0', isPresent: false, needArrow: false},
          {name: '收入同比', prop: 'srtb', needOrder: true, order: '2', isPresent: true, needArrow: true, newLine: true},
          {name: '成本同比', prop: 'cbtb', needOrder: true, order: '0', isPresent: true, needArrow: true, newLine: true},
          {name: '毛利率', prop: 'mll', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
          {name: '同期', prop: 'tq', needOrder: true, order: '0', isPresent: true, needArrow: true},
          {name: '差异', prop: 'cy', needOrder: true, order: '0', isPresent: true, needArrow: true},
          {name: '占比', prop: 'zb', needOrder: true, order: '0', isPresent: true, needArrow: false}
        ],
        tableContent: list.sort(compareSort('srtb', false))
      }
    }
  }
}
</script>

